@import url(https://fonts.googleapis.com/css2?family=Mr+Dafoe&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:wght@900&display=swap);

.game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}


.image-title {
    max-width: 80vw;
}

#imgjogo {
  width: 10vw;
}

.historyButton {
  width: 270px;
  height: 40px;
  font-size: 15px;
  font-weight: bolder;
}

:root {
  --font-size: 5vw;
  --size: 7vw
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 5vw;
  font-size: var(--font-size);
  font-weight: bold;
  line-height: 34px;
  height: 7vw;
  height: var(--size);
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 7vw;
  width: var(--size);
  max-width: 9vh;
  max-height: 9vh;
}

.square:focus {
  outline: none;
}

@media only screen and (max-width: 426px) {
 .square {
   line-height: 0;
 }
}

.board-wrapper {
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
}

.board-border {
    border-bottom: 2px solid black;
}

.board-row{
    display: flex;
    justify-content: center;
}

.image-wrapper {
    position: absolute;
    width: 100%;
}

.super {
    z-index: 1;
    max-width: 80vw;
}

.super-board-wrapper{
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
}

.board-border-right {
    border-right: 2px solid black;
}

.wrapper{
  display: flex;
  align-self: center;
  justify-content: center;
  flex-direction: column;
  font-size: 4vw;
}

.title,
.subtitle {
  margin: 0;
}

.title {
  font-family: 'Exo';
  position: relative;
  margin: 0;
  -webkit-transform: skew(-15deg);
          transform: skew(-15deg);
  letter-spacing: 0.03em;

}

.title::after {
   content: '';
   position: absolute;
   top: -0.1em;
   right: 0.05em;
   width: 0.4em;
   height: 0.4em;
   background:
           radial-gradient(white 3%, rgba(white, 0.3) 15%, rgba(white, 0.05) 60%, transparent 80%),
           radial-gradient(rgba(white, 0.2) 50%, transparent 60%) 50% 50% / 5% 100%,
           radial-gradient(rgba(white, 0.2) 50%, transparent 60%) 50% 50% / 70% 5%;
   background-repeat: no-repeat;
}

.title span:last-child {
  position: absolute;
  left: 0;
  top: 0;
  background-image: linear-gradient(#032d50 25%, #00a1ef 35%, white 50%, #20125f 50%, #8313e7 55%, #ff61af 75%);
  -webkit-text-stroke: 0.01em #94a0b9;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.subtitle {
  font-family: 'Mr Dafoe';
  margin: 0;
  margin-top: -0.6em;
  font-weight: 100;
  color: white;
  text-shadow: 0 0 0.05em #fff, 0 0 0.2em #2afe05, 0 0 0.3em #007916;
  -webkit-transform: rotate(-7deg);
          transform: rotate(-7deg);
}

@media only screen and (min-width: 700px) {
  .wrapper {
    flex-direction: row;
    align-items: baseline;
  }
  .subtitle {
    margin-left: 10px;
  }
}

