@import url('https://fonts.googleapis.com/css2?family=Mr+Dafoe&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo:wght@900&display=swap');

.wrapper{
  display: flex;
  align-self: center;
  justify-content: center;
  flex-direction: column;
  font-size: 4vw;
}

.title,
.subtitle {
  margin: 0;
}

.title {
  font-family: 'Exo';
  position: relative;
  margin: 0;
  transform: skew(-15deg);
  letter-spacing: 0.03em;

}

.title::after {
   content: '';
   position: absolute;
   top: -0.1em;
   right: 0.05em;
   width: 0.4em;
   height: 0.4em;
   background:
           radial-gradient(white 3%, rgba(white, 0.3) 15%, rgba(white, 0.05) 60%, transparent 80%),
           radial-gradient(rgba(white, 0.2) 50%, transparent 60%) 50% 50% / 5% 100%,
           radial-gradient(rgba(white, 0.2) 50%, transparent 60%) 50% 50% / 70% 5%;
   background-repeat: no-repeat;
}

.title span:last-child {
  position: absolute;
  left: 0;
  top: 0;
  background-image: linear-gradient(#032d50 25%, #00a1ef 35%, white 50%, #20125f 50%, #8313e7 55%, #ff61af 75%);
  -webkit-text-stroke: 0.01em #94a0b9;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.subtitle {
  font-family: 'Mr Dafoe';
  margin: 0;
  margin-top: -0.6em;
  font-weight: 100;
  color: white;
  text-shadow: 0 0 0.05em #fff, 0 0 0.2em #2afe05, 0 0 0.3em #007916;
  transform: rotate(-7deg);
}

@media only screen and (min-width: 700px) {
  .wrapper {
    flex-direction: row;
    align-items: baseline;
  }
  .subtitle {
    margin-left: 10px;
  }
}
